import React, { PureComponent } from 'react'

import { FaDiscord, FaCopy } from 'react-icons/fa'
import { FcGoogle } from 'react-icons/fc'

import '../styles/contact.css'

export default class Contact extends PureComponent {
    constructor(props) {
        super(props)

        this.state = {
            showGmailCopied: false,
            showDiscordCopied: false
        }
    }

    handleCopyGmail = () => {
        navigator.clipboard.writeText('marcocoimbra01@gmail.com')

        this.setState({ showGmailCopied: true })

        setTimeout(() => {
            this.setState({
                showGmailCopied: false
            })
        }, 1000)
    }

    handleCopyDiscord = () => {
        navigator.clipboard.writeText('mazc#9221')

        this.setState({ showDiscordCopied: true })

        setTimeout(() => {
            this.setState({
                showDiscordCopied: false
            })
        }, 1000)
    }

    render() {
        return <div id="contact">
            <div style={{ marginBottom: 20 }}>
                <span className="contact-title">Contact</span>
            </div>

            <div>
                <div className="social-div"
                title="click to copy" 
                onClick={this.handleCopyGmail}>
                    <FcGoogle style={{ fontSize: 30 }}/>
                    <span>marcocoimbra01@gmail.com</span>
                    <FaCopy />

                    {this.state.showGmailCopied && <span className="copied">Copied</span>}
                </div>

                <div className="social-div"
                title="click to copy"
                onClick={this.handleCopyDiscord}>
                    <FaDiscord style={{ color: '#5865f2', fontSize: 30 }}/>
                    <span>mazc#9221</span>
                    <FaCopy/>

                    {this.state.showDiscordCopied && <span className="copied">Copied</span>}
                </div>
            </div>
        </div>
    }
}