function handleErrors(response, options={}) {
    if (!response.ok) {
        console.log('requester error: ', response.statusText)
    }
        
    return response
}

export const sendJson = (url, body, options={ alert: true }) => {
    return new Promise((res) => {
        setTimeout(() => {
            res({ success: false, message: 'Request timeout. Try again later.' })
        }, options.timeout || 10000)

        fetch(url, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(body)
        })
        .then((e) => handleErrors(e, options))
        .then(async response => {
            const json = await response.json()

            res(json)
        })
        .catch(() => {
            res({ success: false, message: 'Connection problem. Try again later.' })
        })
    })
}