import React, { PureComponent } from 'react'

import '../styles/button.css'

export default class Button extends PureComponent {
    render() {
        let style = {}

        if(this.props.style) {
            style = { ...this.props.style }
        }

        const onClick = this.props.onClick || (() => {})

        return <button 
            className="button"
            title={this.props.title}
            style={style}
            onClick={onClick} >
                {this.props.children}
            </button>
    }
}