import React, { PureComponent } from 'react'

import { FaAngleLeft, FaAngleRight } from 'react-icons/fa'

import '../styles/imgSlider.css'

export default class ImgSlider extends PureComponent {
    constructor(props) {
        super(props)

        this.images = this.props.images || []

        this.state = {
            active: 0
        }
    }

    componentDidMount() {
        this.slideInterval = setInterval(() => {
            this.handleNext()
        }, 5000)
    }

    componentWillUnmount() {
        if(this.slideInterval) {
            clearInterval(this.slideInterval)

            this.slideInterval = false
        }
    }

    componentDidUpdate() {
        if(this.props.onChange) {
            this.props.onChange({
                ...this.images[this.state.active],
                index: this.state.active
            })
        }
    }

    handlePrev = () => {
        const { active } = this.state

        let next = active - 1

        if(next < 0) {
            next = this.images.length - 1
        }

        this.setState({ 
            active: next
        })
    }

    handleNext = () => {
        const { active } = this.state

        let next = active + 1

        if(next >= this.images.length) {
            next = 0
        }

        this.setState({ 
            active: next
        })
    }

    render() {
        const { active } = this.state

        const style = {}

        return <div className="imgSlider" style={style}>
            <div className="arrow-icon-div">
                <div></div>
                <FaAngleLeft className="arrow-icon-border left" />
                <FaAngleLeft className="arrow-icon left" onClick={this.handlePrev}/>
            </div>
            
            <div className="content">
                <div className="img-div">
                    {this.images.map((img, index) => {
                        return <img key={img.src} src={img.src} className={active === index ? '' : 'hidden'}/>
                    })}
                </div>
                
                <div className="index-div">
                    {this.images.map((img, index) => {
                        return <div key={img.src} className={`slider-index${active === index ? ' active' : ''}`} onClick={() => {
                            this.setState({ active: index })
                        }}/>
                    })}
                </div>
            </div>
            
            <div className="arrow-icon-div">
                <div></div>
                <FaAngleRight className="arrow-icon-border right" />
                <FaAngleRight className="arrow-icon right" onClick={this.handleNext}/>
            </div>
        </div>
    }
}