import React, { PureComponent } from 'react'

import '../styles/header.css'

function getMainApp() {
    return document.getElementById('mainApp')
}

export default class Header extends PureComponent {
    constructor(props) {
        super(props)

        this.state = {
            scrollY: 0,
            activeLink: 'none'
        }
    }

    componentDidMount() {
        const mainApp = getMainApp()

        mainApp.addEventListener('scroll', this.handleScroll)

        this.setState({
            scrollY: mainApp.scrollTop
        })
    }

    componentWillUnmount() {
        getMainApp().removeEventListener('scroll', this.handleScroll)
    }

    handleScroll = () => {
        const scrollY = getMainApp().scrollTop

        const appAbout = document.getElementById('app-about')
        const appAboutBox = appAbout.getBoundingClientRect()

        let activeLink = this.state.activeLink

        if(scrollY > appAboutBox.height) {
            activeLink = 'contact'
        } else {
            activeLink = 'nft-desktop'
        }

        this.setState({ scrollY, activeLink })
    }

    handleClickNFTDesktop = () => {
        if(this.state.activeLink !== 'nft-desktop') {
            getMainApp().scrollTo({ top: 0, behavior: 'smooth' })

            this.setState({ activeLink: 'nft-desktop' })
        }
    }

    handleClickContact = () => {
        if(this.state.activeLink !== 'contact') {
            const contactDiv = document.getElementById('contact')

            const box = contactDiv.getBoundingClientRect()
    
            getMainApp().scrollTo({ top: box.y, behavior: 'smooth' })

            this.setState({ activeLink: 'contact' })
        }
    }

    render() {
        const { activeLink } = this.state

        let style = {}

        if(this.props.style) {
            style = { ...this.props.style }
        }

        return <div id="header" style={style}>
            <div id="header-logo">
                <img src="images/logo.png" width={260} onClick={() => {
                    window.open('https://www.mazc.art', '_blank').focus()
                }} />
            </div>

            <div id="header-links">
                <a onClick={this.handleClickNFTDesktop} className={activeLink === 'nft-desktop' ? 'active' : ''}>NFT Desktop</a>
                <a onClick={this.handleClickContact} className={activeLink === 'contact' ? 'active' : ''}>Contact</a>
            </div>

            <div id="header-actions">
            </div>
        </div>
    }
}