import React, { Fragment, PureComponent } from 'react'

import ImgSlider from './imgSlider'
import Button from './button'
import Modal from './modal'
import InputText from './inputText'

import cryptoPrice from 'crypto-price'
import { sendJson } from '../request'

import '../styles/nft-desktop.css'

const AppContent = {
    title: 'NFT Desktop',
    description: 'Automated conditional filters for NFT games marketplaces. (Only Axie Infinity at the moment)',
    features: [
        { 
            desc: '1. Create/edit/delete a unlimited number of automated filters'
        },
        {
            desc: '2. Rich filter execution control including update rate, sorting, pagination, pausing and audio alerts'
        },
        {
            desc: '3. Filter Axie marketplace by price range, pureness percentage, body part/ability gene type and more'
        },
        {   
            desc: "4. Fetch complete data from axies instantly with gene parsing and breed probabilities"
        }
    ],
    subscriptions: [
        {
			key: 'one_month',
			title: 'One month subscription',
			priceDolar: 50,
			keyDurationDays: 30
		},
		{
			key: 'two_month',
			title: 'Two months subscription',
			priceDolar: 90,
			keyDurationDays: 60 
		},
		{
			key: 'three_month',
			title: 'Three months subscription',
			priceDolar: 135,
			keyDurationDays: 90
		}
    ]
}

const cryptoTickers = [ 'BTC', 'ETH', 'LTC' ]

async function getCryptoRates() {
    return Promise.all(cryptoTickers.map(ticker => {
        return new Promise(async res => {
            let tickerData = await cryptoPrice.getCryptoPrice('USD', ticker)

            tickerData = tickerData || { price: false }

            res({
                ticker,
                price: tickerData.price
            })
        })
    }))
}

function usd2Crypto(usd, cryptoRate) {
    if(cryptoRate.price === false) {
        return false
    }

    return (usd / cryptoRate.price).toFixed(5)
}

export default class NFTDesktop extends PureComponent {
    constructor(props) {
        super(props)

        this.state = {
            imgIndex: 0,
            cryptoRates: cryptoTickers.map(e => ({ ticker: e, price: false })),
            subscription: false,
            userEmail: '',

            checkoutError: false
        }

        this.images = []
        this.features = []

        for(let i = 1; i <= 4; i++) {
            this.images.push({ src: `images/nftdesktop0${i}.png` })

            this.features.push(AppContent.features[i - 1])
        }

        this.sliderRef = React.createRef()
        this.checkoutModalRef = React.createRef()

        getCryptoRates().then(cryptoRates => {
            if(this._isMounted) {
                this.setState({ cryptoRates })
            } else {
                this.state.cryptoRates = cryptoRates
            }
        })
    }

    componentDidMount() {
        this._isMounted = true
    }

    componentWillUnmount() {
        this._isMounted = false
    }

    handleImgChange = (img) => {
        this.setState({ imgIndex: img.index })
    }

    handleClickFeature = (featureIndex) => {
        this.setState({ imgIndex: featureIndex })

        if(this.sliderRef.current) {
            this.sliderRef.current.setState({ active: featureIndex })
        }
    }

    handleDownloadForWindows = () => {
        document.body.style.cursor = 'progress'

        let a = document.createElement('a')

        a.href = 'https://nftdesktop.nyc3.cdn.digitaloceanspaces.com/NFT-Desktop%20Setup%200.1.0.exe'

        a.download = 'nft-desktop-installer.exe'
        
        a.click()

        a.remove()

        document.body.style.cursor = 'default'
    }

    handleInitCheckout = (subscription) => {
        this.setState({ subscription })

        if(this.checkoutModalRef.current) {
            this.checkoutModalRef.current.open()
        }
    }

    handleReqCheckout = async () => {
        const { userEmail, subscription } = this.state

        if(userEmail === '') {
            this.setCheckoutError('Please input a real email address')

            return
        }

        if(!subscription) {
            this.setCheckoutError('Subscription type not selected')

            return
        }
            
        const response = await sendJson('https://api.mazc.art/payment/coinbase', {
            userEmail,
            subscriptionKey: subscription.key
        })

        if(!response.success) {
            this.setCheckoutError('Checkout request failed: ' + response.error)

            return
        }

        window.open(response.payload.url, '_blank').focus()

        if(this.checkoutModalRef.current) {
            this.checkoutModalRef.current.close()
        }
    }

    setCheckoutError = (checkoutError) => {
        this.setState({ checkoutError }) 

        setTimeout(() => {
            this.setState({ checkoutError: false })
        }, 3000)
    }

    render() {
        const { imgIndex, checkoutError } = this.state

        const style = {}

        return <div id="nft-desktop" style={style}>
            <div id="app-about">
                <div id="app-info">
                    <div id="app-title">
                        <img src="images/app-icon.png"></img>
                        <span>{AppContent.title}</span>
                    </div>

                    <div id="app-desc">
                        <p>{AppContent.description}</p>
                    </div>

                    <div id="app-features">
                        {this.features.map((feature, index) => {
                            return <p key={index} className={imgIndex === index ? 'active' : ''} onClick={() => this.handleClickFeature(index)}>{feature.desc}</p>
                        })}
                    </div>
                </div>

                <ImgSlider ref={this.sliderRef} images={this.images} onChange={this.handleImgChange}/>
            </div>

            <div id="app-distribution">
                <div id="app-pricing">
                    {AppContent.subscriptions.map(subscription => {
                        return <div className="pricing-card" key={subscription.key} onClick={() => {
                            this.handleInitCheckout(subscription)
                        }}>
                            <span className="pricing-title">{subscription.title}</span>
                            <span className="pricing-value">${subscription.priceDolar}</span>

                            {this.state.cryptoRates.map(cryptoRate => {
                                if(cryptoRate.price === false) {
                                    return <Fragment key={cryptoRate.ticker}/>
                                }

                                return <span key={cryptoRate.ticker}>{cryptoRate.ticker}: {usd2Crypto(subscription.priceDolar, cryptoRate)}</span>
                            })}

                            <Button onClick={(e) => {
                                e.stopPropagation()
                                
                                this.handleInitCheckout(subscription)
                            }}>Buy</Button>
                        </div>
                    })}
                </div>

                <div id="app-download">
                    <Button onClick={this.handleDownloadForWindows}>Download for Windows</Button>
                    <span style={{ fontWeight: 'bold', marginTop: 5 }}>Includes a 2 hours TRIAL key</span>
                </div>
            </div>

            <Modal ref={this.checkoutModalRef}>
                <div id="checkout-modal">
                    <InputText placeholder={"Email for contact"} onChange={userEmail => this.setState({ userEmail })}/>
                    <span>The subscription key will be sent to this email after the payment is confirmed.</span>
                    <Button onClick={this.handleReqCheckout}>Ok</Button>

                    {checkoutError && <span style={{ color: 'red' }}>{checkoutError}</span>}
                </div>
            </Modal>
        </div>
    }
}