import React, { PureComponent, Fragment } from 'react'

import '../styles/modal.css'

export default class Modal extends PureComponent {
    constructor(props) {
        super(props)

        this.state = {
            active: false
        }
    }

    componentDidMount() {
        this._isMounted = true

        window.addEventListener('keyup', this.handleKeyPress)
    }

    componentWillUnmount() {
        this._isMounted = false
        
        window.removeEventListener('keyup', this.handleKeyPress)
    }

    handleKeyPress = (e) => {
        if(!this._isMounted)
            return

        if(e.key === 'Escape') {
            this.setState({
                active: false
            })
        }
    }

    open = (callback) => {
        this.setState({
            active: true
        }, () => {
            if(callback)
                callback()
        })
    }

    close = () => {
        this.setState({
            active: false
        })
    }

    handleClose = () => {
        this.close()
    }

    render() {
        if(!this.state.active) {
            return <Fragment />
        }

        const style = this.props.style || {}
        const contentStyle = this.props.contentStyle || {}

        let className = 'modal'

        if(this.props.className) {
            className += ' ' + this.props.className
        }

        return <div style={style} className={className} onClick={this.handleClose}>
            <div style={contentStyle} className="modal-content" onClick={e => { e.stopPropagation() }}>
                {this.props.children}
            </div>
        </div>
    }
}